(function () {
    'use strict';
    var angular = require('angular');
    require('./exception.component.scss');

    var exception = angular.module('app').component('exception', {
        template: require('./exception.component.html'),
        controller: ['appState',function (appState) {
            var vm = this;
            vm.$onInit = function () {
                appState.clearAll();
            };
            vm.exit = function () {
                window.location.href = "http://www.hsbc.co.in/";
            }
        }]
    });

    module.exports = exception.name;
})();


